<template>
  <div>
    <v-container grid-list-md>
      <v-layout row wrap>
        <v-flex xs3 v-for="order in orders" :key="order.key">
          Order Time: {{order.time}}
          <v-container grid-list-md>
            <v-layout row wrap>
            <v-flex xs3 v-for="movie in ObjToArray(order.movies)" :key="movie.key">
              <v-img
                :src="movie.Poster"
                heigh="255"
              ></v-img>
            </v-flex>
            </v-layout>
          </v-container>
        </v-flex>
      </v-layout>
    </v-container>

    <v-dialog width="300" v-model="loading">
      <v-card color="red darken-3" dark>
        <v-card-text>
          Loading Orders...
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>

  import MS from '../firebaseCRUD'

  export default {
    beforeMount(){
      this.loading = true;
      setTimeout(() => {this.getOrders()}, 2000)
    },
    components: {
    },
    props: {
      ActiveUser: Object
    },
    data() {
      return {
        subColor: "#ab302f",
        orders: [],
        loading: false
      };
    },
    methods: {
      getOrders(){
        MS.getOrders(this.ActiveUser.key).then(orders => {
           this.orders = orders;
        });
        this.loading = false;
      },
      ObjToArray(obj){
        var arr = new Array(Object.keys(obj).length);
        for(var key in obj){
          arr[key] = obj[key];
        }
        return arr;
      }
    }
  };
</script>

<style scoped>
  #form {
    margin-right: 5%;
    margin-left: 5%;
    padding-top:10px;
    padding-bottom:30px;
    text-align: left;

  }

  .v-btn--large{
    padding: 10px !important;
  }

  .header {
    background-color: rgba(185, 134, 12, 0.79);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
</style>



